.popup{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10!important;
    background-color: #353b48e1;
    top: 0;
    left: 0;
}

.x{
    position: absolute;
    background-color: #808080c7;
    padding: 0.6rem;
    right: 2rem;
    border-radius: 30%;
    top:1.3rem;
    z-index: 15;
    cursor: pointer;
}

.navbar{
    z-index: 1;
}

.wrapper-login{
    position: relative;
    background-color: #297fb9c9;
    padding: 5rem;
    border-radius: 10%;
    width: 45vw;
    height: 30rem;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    box-shadow: -2px 2px 10px 5px #2a445e;
}
@media all and (min-width: 1100px) {
    .wrapper-login{
        width: 28vw;
        height: 28rem;
    }
  }
  @media all and (max-width: 600px) {
    .wrapper-login{
        width: 75vw;
        height: 28rem;
    }
  }

  .center{
      position: relative;
      display: inline-block;
      left: 50%;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
  }

.labelControl{
    opacity: 0;
    position: relative;
    top:-10px;
    left:10px;
    transition: 0.3s all;
    -webkit-backface-visibility: none;
            backface-visibility: none;
    color: #34495e;
}
.form-control{
    color: #2c3e50;
    font-weight: bold;
}
input.form-control:focus + label.labelControl{
    opacity: 1;
    top:8px;
}
input.form-control::-webkit-input-placeholder{
    transition: 0.5s all;
}
input.form-control::-ms-input-placeholder{
    transition: 0.5s all;
}
input.form-control::placeholder{
    transition: 0.5s all;
}
input.form-control:focus::-webkit-input-placeholder{
    opacity: 0;
}
input.form-control:focus::-ms-input-placeholder{
    opacity: 0;
}
input.form-control:focus::placeholder{
    opacity: 0;
}

